import React from 'react';
import DocumentTitle from 'react-document-title';
import Modal from 'src/components/Modal';
import Button from 'src/components/Button';

function noop() {}

const ButtonModalGroup = React.createClass({
    propTypes: {
        title: React.PropTypes.string,
        type: React.PropTypes.string,
        buttonTitle: React.PropTypes.string,
        onClosed: React.PropTypes.func,
        onDismissed: React.PropTypes.func
    },
    getDefaultProps() {
        return {
            onClosed: noop,
            onDismissed: noop
        }
    },
    getInitialState() {
        return {
            isOpen: false
        };
    },
    onClosed() {
        this.close();
        this.props.onClosed();
    },
    onDismissed() {
        this.close();
        this.props.onDismissed();
    },
    open() {
        this.setState({
            isOpen: true
        });
    },
    close() {
        this.setState({
            isOpen: false
        });
    },
    render() {
        let {
            buttonTitle,
            ...props
        } = this.props;

        delete props.onDismissed;
        delete props.onClosed;

        return (
            <div>
                <Button className="btn-primary" onClick={this.open}>{buttonTitle}</Button>
                <Modal isOpen={this.state.isOpen} onClosed={this.onClosed} onDismissed={this.onDismissed} {...props}></Modal>
            </div>
        );
    }
});
const ModalPage = React.createClass({
    getInitialState() {
        return {
            isAlertOpen: false,
            isConfirmOpen: false
        };
    },
    render() {
        return (
            <DocumentTitle title="ModalPage">
                <div>
                    <p>Alert Modal</p>
                    <ButtonModalGroup  
                        buttonTitle="Alert Modal" 
                        boxShadow={false}
                    >
                        这是一个alert窗口
                    </ButtonModalGroup>
                    <p>Confirm Modal</p>
                    <ButtonModalGroup 
                        type="confirm" 
                        title="React-MUI" 
                        buttonTitle="Confirm Modal" 
                        onDismissed={() => {console.log('Confirm Modal was dismissed!')}} 
                        onClosed={() => {console.log('Confirm Modal was closed!')}}
                    >
                        这是一个confirm窗口
                    </ButtonModalGroup>
                </div>
            </DocumentTitle>
        )
    }
});

export default ModalPage;


